.Item {
    padding: 12px;
    transition: transform   300ms cubic-bezier(0.455, 0.03, 0.515, 0.955),
                width       300ms cubic-bezier(0.455, 0.03, 0.515, 0.955),
                height      300ms cubic-bezier(0.455, 0.03, 0.515, 0.955);
    width: 50%;
    height: 50%;

    display: flex;
    flex-direction: row;
    height: 100%;
    width: 100%;
}

.ParentContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: left;

    width: 100%;
    height: 100vh;
    padding-right: 28px;
}

